import React from "react"
import axios from "axios"
import Movie from "./Moives"
import "./App.css"

// App class는 React.Component에서 가져온다.
class App extends React.Component {
  state = {
    isLoading: true,
    movies: [],
  }
  getMoives = async () => {
    // async를 써서 비동기라고 말한것.  "너는 이걸 기달려야 해" 라는 뜻
    // axios는 fetch 위에 있는 작은 layer
    // axios.get 은 빠르지 않음. 기다려야 된다.
    // 우리는 자바스크립트에게 componentDidMount가 끝날때까지 좀 시간이 걸린다고 말해줘야함
    const {
      data: {
        data: { movies },
      },
    } = await axios.get("https://yts.mx/api/v2/list_movies.json/list_movies.json?sort_by=rating")
    // movies.data.data.moives -->  {data: {data :{movies}}}
    // await를 통해 axios를 기다려야 한다는 걸 알음 , async를 쓰지 않으면 await를 쓸 수 없다
    console.log(movies)
    this.setState({ movies: movies, isLoading: false }) // 앞의 movies 는 state의 moives , 뒤의 moives 는 getMoives의 movies
  }
  // component(render)가 mount된 뒤에 발생함
  componentDidMount() {
    this.getMoives()
  }
  render() {
    const { isLoading, movies } = this.state
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading...</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map((
              movie // map으로 부터는 항상 뭔가를 return해야함
            ) => (
              <Movie
                key={movie.id} // key가 유니크한 값을 가지고 있어야함
                id={movie.id}
                year={movie.year}
                title={movie.title}
                summary={movie.summary}
                poster={movie.medium_cover_image}
                genres={movie.genres}
              />
            ))}
          </div>
        )}
      </section>
    )
  }
}

export default App
